<template>
    <main class="main-content">
        <!-- <asset-preview ref="preview"  :asset="currentAsset"></asset-preview> -->
        <div
        class="vue-lb-figure"
        @click.stop>
        <transition
          mode="out-in"
          name="fade">
            <asset-preview ref="preview" v-if="currentAsset" :asset="currentAsset"></asset-preview>
        </transition>

        <div class="vue-lb-footer">
          <div class="vue-lb-footer-info"></div>
          <div class="vue-lb-footer-count">
            <!-- {{ select + 1 }}/{{ images.length }} -->
          </div>
        </div>

      </div>
    </main>
  </template>
  
  <script>
 
  import AssetPreview from '../components/AssetPreview.vue'
  
  export default {
    components: {
      AssetPreview
    },

    data() {
      return {
        currentAsset:null
      };
    },
  
  
    // TODO: Remove this so Mika doesn't get mad :troll:
    mounted() {
      if (this.$route.query.asset != undefined) {
          this._fetchAsset(this.$route.query.asset)
      }
    },
  
    methods: {
      _fetchAsset(uid){
        let _this = this
        this.loading = true;
  
        _this.$nuxeo
          .repository('default', {
            schemas: "*",
            headers: {
              "enrichers.document":
                "thumbnail,preview,permissions,codename,relatedDevices",
              "X-NXfetch.aggregate": "key",
              "fetch.document": "properties",
              "X-NXtranslate.directoryEntry": "label",
            },
            // queryParams: this.queryParams,
          })
          .fetch(uid)
          .then(function(res) {
            _this.currentAsset = res
          })
          .catch(function(error) {
            throw new Error(error);
          });
      },
    },

  };
  </script>
  
  <style lang="scss">
  .main-content{
    padding: 0 4rem 3.125rem;
  }
  </style>
  