<script>
import { constants } from "../constants.js";
import ImgPreview from "./ImgPreview.vue";

import "video.js/dist/video-js.css";
import { videoPlayer } from "vue-video-player";

export default {
  components: {
    ImgPreview,
    videoPlayer,
  },

  props: {
    asset: {
      type: Object,
    },
  },

  computed: {
    isShowAddBtn() {
      if (this.asset.contextParameters.permissions.indexOf("Download") != -1) {
        return true;
      }
      return false;
    },
    nonPictureUrl() {
      return this.asset.contextParameters.preview.url;
    },
    isPdf() {
      return (
        this.asset.properties["file:content"] !== "undefined" &&
        this.asset.properties["file:content"]["mime-type"] !== null &&
        this.asset.properties["file:content"]["mime-type"] === "application/pdf"
      );
    },
    pdfUrl() {
      return this.asset.contextParameters.preview.url;
    },
    imageUrl() {
      return (
        this.asset.properties["picture:views"].length &&
        this.asset.properties["picture:views"][3].content.data
      );
    },
    playerOptions() {
      return {
        // videojs options
        muted: true,
        playbackRates: [0.7, 1.0, 1.5, 2.0],
        sources: [
          {
            type: "video/mp4",
            src: this.asset.properties["vid:transcodedVideos"][1].content.data,
          },
        ],
        poster:
          this.asset.properties["picture:views"].length &&
          this.asset.properties["picture:views"][1].content.data,
      };
    },
    title() {
      return this.asset.title;
    },
    description() {
      if (this.asset.properties["dc:description"]) {
        return this.asset.properties["dc:description"];
      }
      return "";
    },
    assetType() {
      if (this.asset.properties["amz:asset_type"]) {
        return this.asset.properties["amz:asset_type"];
      }
      return "";
    },
    assetCategory() {
      if (this.asset.properties["amz:asset_category"]) {
        return this.asset.properties["amz:asset_category"];
      }
      return "";
    },
    originalCreatorName() {
      if (this.asset.properties["amz:original_creator_name"]) {
        return this.asset.properties["amz:original_creator_name"];
      }
      return "";
    },
    originalCreatorRole() {
      if (this.asset.properties["amz:original_creator_role"]) {
        return this.asset.properties["amz:original_creator_role"];
      }
      return "";
    },
    language() {
      if (this.asset.properties["amz:language"]) {
        return this.asset.properties["amz:language"];
      }
      return "";
    },
    isRetouchedVersion() {
      if (this.asset.properties["amz:is_retouched_version"]) {
        return this.asset.properties["amz:is_retouched_version"];
      }
      return "";
    },
    owner() {
      if (this.asset.properties["dc:publisher"]) {
        return this.asset.properties["dc:publisher"];
      }
      return "";
    },
    rightsType() {
      if (this.asset.properties["usage:rights_type"]) {
        return this.asset.properties["usage:rights_type"];
      }
      return "";
    },
    channel() {
      if (this.asset.properties["usage:channel"]) {
        return this.asset.properties["usage:channel"];
      }
      return "";
    },
    subChannel() {
      if (this.asset.properties["usage:subchannel"]) {
        return this.asset.properties["usage:subchannel"];
      }
      return "";
    },
    geography() {
      var geographies = this.asset.properties["usage:geography"];
      return geographies
        .map(function(i) {
          return i.properties.label;
        })
        .join(",");
    },
    approvedLanguage() {
      var languages = this.asset.properties["usage:language"];
      return languages
        .map(function(i) {
          if(typeof i == 'string'){
            return i
          }else{
            return i.properties.label 
          }
        })
        .join(",");
    },
    expire() {
      if (this.asset.properties["dc:expired"]) {
        const timestamp = this.asset.properties["dc:expired"];
        const date = new Date(timestamp);
        const formattedDate = date.toLocaleDateString("en-US", {
          month: "long",
          day: "numeric",
          year: "numeric",
        });
        return formattedDate;
      }
      return "";
    },
    restriction() {
      if (this.asset.properties["dc:rights"]) {
        return this.asset.properties["dc:rights"];
      }
      return "";
    },
    workspace() {
      if (this.asset.properties["relations:workspaceLocation"]) {
        return this.asset.properties["relations:workspaceLocation"].title;
      }
      return "";
    },
    product() {
      var product = this.asset.contextParameters.relatedDevices;
      if (!product || product === null || product.length === 0) {
        return null;
      }
      return product
        .map(function(device) {
          return device;
        })
        .join(",");
    },

    keywords() {
      let keywords = this.asset.properties["amzkeyword:keywords"];
      if (keywords === null || keywords.length === 0) {
        return null;
      }
      // return keywords.map(function (item) {
      //   return item.label;
      // }).join(',');
      return keywords;
    },

    currentUrl() {
      if (!this.$route.query.asset) {
        let pathname = '' 
        window.location.pathname == '/' ? pathname = '' : pathname = window.location.pathname
        return `${window.location.origin}${pathname}#detail?asset=${this.asset.uid}`;
      }
    },
  },

  methods: {
    stop() {
      if (this.$refs.videoPlayer) {
        this.$refs.videoPlayer.$refs.video.pause();
      }
    },

    addToCart() {
      this.$nuxeo
        .operation("javascript.CART_AddToCart")
        .input(this.asset.uid)
        .params({
          cartId: this.$store.state.cartId,
        })
        .execute()
        .then(
          function() {
            this.$toasted.success(
              this.$t("message.assetPreview-added-to-cart"),
              constants.TOASTED_DEFAULT_OPTIONS
            );
            this.$store.dispatch("updateCart");
          }.bind(this)
        )
        .catch(function(error) {
          this.$toasted.error(
            this.$t("message.assetPreview-unable-to-add"),
            constants.TOASTED_DEFAULT_OPTIONS
          );
          throw error;
        });
    },

    removeFromCart() {
      this.$nuxeo
        .operation("javascript.CART_RemoveFromCart")
        .input(this.asset.uid)
        .params({
          cartId: this.$store.state.cartId,
        })
        .execute()
        .then(
          function() {
            this.$toasted.success(
              this.$t("message.assetPreview-removed-from-cart"),
              constants.TOASTED_DEFAULT_OPTIONS
            );
            this.$store.dispatch("updateCart");
          }.bind(this)
        )
        .catch(function(error) {
          this.$toasted.error(
            this.$t("message.assetPreview-unable-to-remove"),
            constants.TOASTED_DEFAULT_OPTIONS
          );
          throw error;
        });
    },

    isInCart() {
      var isInCart = this.$store.getters.isInCart(this.asset.uid);
      return isInCart;
    },

    redirectToSearchPage(keyword) {
      let queryParams = {};
      queryParams.facetedKeyword_keywords_label = keyword;
      this.$router.push({
        path: "/search",
        query: queryParams,
      });
      this.$emit("onCloseLightBox", queryParams);
    },
  },
};
</script>

